section#containerOfHPAtlas{
	margin-top: 150px;
}
section#containerOfHPAtlas div.lineOFMessageHP{
	padding-top: 20px;
}
section#containerOfHPAtlas h2.title, section#containerOfHPAtlas div.header li.ar, section#containerOfHPAtlas div.header li.it, section#containerOfHPAtlas div.header li.es{
	display: none;
}
section#containerOfHPAtlas div.blocOfMenu li{
	list-style: none;
	margin-top: 5px;
}
section#containerOfHPAtlas div.blocOfMenu li a{
	color: #000;
	background-color: none;
	position: relative;
	display: block;
	padding: 10px 15px;
	background-color: #428bca;
	border-radius: 4px;
}
section#containerOfHPAtlas div.blocOfMenu li a.active{
	color: #2a6496;
	background-color: #fff;
	border-radius: 4px;
	position: relative;
	display: block;
	padding: 10px 15px;
}
section#containerOfHPAtlas div.blocOfMenu li a:hover, section#containerOfHPAtlas div.blocOfMenu li a:focus {
	text-decoration: none;
	background-color: #eee;
	color: #2a6496;
}
section#containerOfHPAtlas div#block-nice_menus-16, section#containerOfHPAtlas  div#block-nice_menus-14, section#containerOfHPAtlas div#block-nice_menus-11{
	display: none;
}

